<template>
    <div id="contact-us">
        <v-row class="d-flex align-center" style="height:95vh" v-if="visible">
            <v-col lg="5" col="12" class="d-flex justify-center">
                <div style="position:relative; margin-top: 15%">
                    <img :class="{'cloud cl1': true, 'cloudEnd': animated}" src="/img/get-quote-cloud2.svg" style="z-index:1; ">
                    <img :class="{'balloon': true, 'balloonEnd': animated}" src="/img/get-quote-balloon.svg" style=" position: relative; z-index: 2;">
                    <img :class="{'cloud': true, 'cloudEnd': animated}" src="/img/get-quote-cloud1.svg" style="z-index:2; bottom: 20%; left:0; position:absolute">
                </div>
            </v-col>
            <v-col lg="7" col="12" class="d-flex justify-center">
                <div :class="{'display-3 font-weight-bold secondary--text': true, 'textEnd': animated}"
                     style="display: inline-block;">
                    <span class="cu-text1"> , </span><br>
                    <span class="cu-text2 primary--text"> </span>
                    <span class="cu-text_hide" style="opacity: 0;"> </span>
                    <v-btn class="cu-button" 
                           style="display: block; margin-top: 15%" 
                           rounded 
                           color="primary" 
                           large dark
                           v-on:click="form_go()">
                      </v-btn>
                </div>
            </v-col>
        </v-row>
        <v-row class="d-flex align-center" style="height:95vh" v-else>
            <v-row>
                <v-col col="12" lg="8" class="d-flex justify-center">
                    <v-form ref="form"
                            v-model="valid"
                            :lazy-validation="lazy"
                            style="width:100%"
                            v-if="step_1"
                            class="slide-step">
                        <v-text-field v-model="name"
                                      :counter="10"
                                      :rules="nameRules"
                                      label="Name"
                                      required></v-text-field>

                        <v-text-field v-model="email"
                                      :rules="emailRules"
                                      label="E-mail"
                                      required></v-text-field>
                        <v-checkbox v-model="checkbox"
                                    :rules="[v => !!v || 'You must agree to continue!']"
                                    label="Do you agree?"
                                    required></v-checkbox>
                        <v-btn class="mr-5 font-weight-bold" rounded color="primary" outlined large dark v-on:click="form_back('start')"></v-btn>
                        <v-btn :disabled="!valid"
                               color="primary"
                               class="float-right"
                               rounded
                               large
                               @click="validate"
                               v-on:click="next_step(2)">
                            
                        </v-btn>
                    </v-form>
                    <div v-if="step_2" class="slide-step font-weight-bold">
                            <v-row>
                                <v-col lg="4">
                                    <input type="checkbox" id="c1" value="Mobile Development">
                                    <label for="c1" class="pa-5">
                                        <img class="d-block mb-7" src="img/icon/icon1.svg" />
                                        <span>Web </span>
                                    </label>
                                </v-col>
                                <v-col lg="4">
                                    <input type="checkbox" id="c2" value="Mobile Development">
                                    <label for="c2" class="pa-5">
                                        <img class="d-block mb-7" src="img/icon/icon2.svg" />
                                        <span>Mobile </span>
                                    </label>
                                </v-col>
                                <v-col lg="4">
                                    <input type="checkbox" id="c3" value="Mobile Development">
                                    <label for="c3" class="pa-5">
                                        <img class="d-block mb-7" src="img/icon/icon3.svg" />
                                        <span>UI/UX Design</span>
                                    </label>
                                </v-col>
                                <v-col lg="4">
                                    <input type="checkbox" id="c4" value="Mobile Development">
                                    <label for="c4" class="pa-5">
                                        <img class="d-block mb-7" src="img/icon/icon4.svg" />
                                        <span></span>
                                    </label>
                                </v-col>
                            </v-row>
                            <v-btn class="mr-5 font-weight-bold" rounded color="primary" outlined large dark v-on:click="form_back(1)"></v-btn>
                            <v-btn color="primary"
                                   class="float-right"
                                   rounded
                                   large
                                   v-on:click="next_step(3)">
                                
                            </v-btn>
                    </div>
                    <div v-if="step_3" class="slide-step font-weight-bold">
                        <input type="radio" id="r1" name="budget"/>
                        <label for="r1" class="pa-5">
                            <span>&#8381;0 - 10K</span>
                        </label>
                        <input type="radio" id="r2" name="budget"/>
                        <label for="r2" class="pa-5">
                            <span>&#8381;10 - 25K</span>
                        </label>
                        <input type="radio" id="r3" name="budget"/>
                        <label for="r3" class="pa-5">
                            <span>&#8381;25 - 50K</span>
                        </label>
                        <input type="radio" id="r4" name="budget"/>
                        <label for="r4" class="pa-5">
                            <span>&#8381;50 - 100K</span>
                        </label>
                        <input type="radio" id="r5" name="budget"/>
                        <label for="r5" class="pa-5">
                            <span>&#8381;100K+</span>
                        </label>
                        <v-btn class="mr-5 font-weight-bold" rounded color="primary" outlined large dark v-on:click="form_back(2)"></v-btn>
                        <v-btn color="primary"
                               class="float-right"
                               rounded
                               large
                               v-on:click="next_step(4)">
                            
                        </v-btn>
                    </div>
                    <div v-if="step_4" class="slide-step">
                        <span> </span>
                        <textarea rows="10" style="display: block; width: 100%; border: 1px solid black; border-radius: 5px; margin-bottom: 15px">
                          
                        </textarea>
                        <v-btn class="mr-5 font-weight-bold" rounded color="primary" outlined large dark v-on:click="form_back(3)"></v-btn>
                        <v-btn color="primary"
                               class="float-right"
                               rounded
                               large
                               v-on:click="next_step('finish')">
                            
                        </v-btn>
                    </div>
                </v-col>
                <v-col class="slide-right
                       contact-us-panel
                       white--text 
                       d-flex 
                       align-center" col="12" lg="4">
                    <div class="pl-12 pr-12">
                        <div class="d-flex align-center mb-8"
                             v-bind:style="{opacity: op_step1}">
                            <span class="d-flex align-center justify-center">1</span>
                            <div>
                                <p class="font-weight-bold mb-1">  ?</p>
                                <p class="mb-0">,    </p>
                            </div>
                        </div>
                        <div class="d-flex align-center mb-8" 
                             v-bind:style="{opacity: op_step2}">
                            <span class="d-flex align-center justify-center">2</span>
                            <div>
                                <p class="font-weight-bold mb-1">    ?</p>
                                <p class="mb-0">   <br></p>
                            </div>
                        </div>
                        <div class="d-flex align-center mb-8"
                             v-bind:style="{opacity: op_step3}">
                            <span class="d-flex align-center justify-center">3</span>
                            <div>
                                <p class="font-weight-bold mb-1"> ?</p>
                                <p class="mb-0">    <br>  </p>
                            </div>
                        </div>
                        <div class="d-flex align-center mb-8"
                             v-bind:style="{opacity: op_step4}">
                            <span class="d-flex align-center justify-center">4</span>
                            <div>
                                <p class="font-weight-bold mb-1">   ?</p>
                                <p  class="mb-0">    <br>   </p>
                            </div>
                        </div>
                    </div>
                </v-col>
            </v-row>
        </v-row>
    </div>
</template>
<script>
    export default {
        name: "contact-us",
        data: () => ({
            visible: true,
            valid: true,
            name: '',
            nameRules: [
                v => !!v || 'Name is required',
                v => (v && v.length <= 10) || 'Name must be less than 10 characters',
            ],
            email: '',
            emailRules: [
                v => !!v || 'E-mail is required',
                v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
            ],
            select: null,
            checkbox: false,
            lazy: false,
            step_1: true,
            step_2: false,
            step_3: false,
            step_4: false,
            op_step1: 1,
            op_step2: .3,
            op_step3: .3,
            op_step4: .3,
            animated: false
        }),

        methods: {
            form_go() {
                this.animated = true;
                setTimeout(function () {
                    this.$root.btnShow_menu = false;
                    this.visible = false;
                }.bind(this), 1000);
            },
            form_back(step) {
                if (step == 'start') {
                    this.animated = false;
                    this.visible = true;
                    this.$root.btnShow_menu = true;
                }
                else if (step == 1) {
                    this.step_2 = false;
                    this.step_1 = true;
                    this.op_step2 = .3;
                    this.op_step1 = 1;
                }
                else if (step == 2) {
                    this.step_3 = false;
                    this.step_2 = true;
                    this.op_step3 = .3;
                    this.op_step2 = 1;
                }
                else if (step == 3) {
                    this.step_4 = false;
                    this.step_3 = true;
                    this.op_step4 = .3;
                    this.op_step3 = 1;
                }
            },
            validate() {
                this.$refs.form.validate()
            },
            reset() {
                this.$refs.form.reset()
            },
            resetValidation() {
                this.$refs.form.resetValidation()
            },
            next_step(step) {
                if (step == 2) {
                    this.step_1 = false;
                    this.step_2 = true;
                    this.op_step2 = 1;
                    this.op_step1 = .3;
                }
                else if (step == 3) {
                    this.step_2 = false;
                    this.step_3 = true;
                    this.op_step3 = 1;
                    this.op_step2 = .3;
                }
                else if (step == 4) {
                    this.step_3 = false;
                    this.step_4 = true;
                    this.op_step4 = 1;
                    this.op_step3 = .3;
                }
                else if (step == 'finish') {
                    alert('');
                }
            }
        },
        mounted: function () {
            this.$root.btnShow_menu = true
        }
    }
</script>
<style>
    .cloud {
        animation: cloud 1s ease-out;
        position: absolute;
    }
    .cloud.cl1{
        right: 0;
    }
    .balloon{
        animation: balloon 1s ease-out;
    }
    .cu-text1, .cu-text2{
        position: absolute;
    }
    .cu-text1{
        animation: cu-text1 3s ease-out;
        animation-fill-mode: forwards;
    }
    .cu-text2{
        animation: cu-text2 1.5s ease-out;
        animation-delay: .5s;
        animation-fill-mode: forwards;
        opacity: 0;
    }
    .cu-button{
        animation: cu-button 1s ease-out;
        animation-delay: 3s;
        animation-fill-mode: forwards;
        opacity: 0;
    }
    .balloonEnd{
         animation: balloonEnd 1s ease-in;
         animation-fill-mode: forwards;
    }
    .cloudEnd{
        animation: cloudEnd 1s ease-in;
        animation-fill-mode: forwards;
    }
    @keyframes cloud {
        from{
            transform: translate(-50vw, 0vh);
            opacity: 0;
        }
    }
    @keyframes balloon {
        from{
            transform: translate(-50vw, 20vh) rotate(30deg);
            opacity: 0;
        }
    }
    @keyframes balloonEnd {
        to{
            transform: translate(10vw, -100vh) rotate(30deg);
        }
    }
    @keyframes cloudEnd {
        to{
            transform: translate(30vw, 0vh);
            opacity: 0;
        }
    }
    @keyframes cu-text1 {
        0%{
            opacity: 0;
            transform: translate(50vw, 4vh);
        }
        40%{
            opacity: 1;
            transform: translate(0vw, 4vh);
        }
        60%{
            opacity: 1;
            transform: translate(0vw, 4vh);
        }
        100%{
            opacity: 1;
            transform: translate(0vw, 0vh);
        }
    }
    @keyframes cu-text2 {
        from{
            transform: translate(0vw, 80vh);
            opacity: 0;
        }
        to{
            opacity: 1;
        }
    }
    @keyframes cu-button {
        to{
            opacity: 1;
        }
    }

    .contact-us-panel {
        background: var(--v-secondary-base);
        z-index: 5;
        position: absolute;
        right: 0;
        top:0;
        height: 100vh;
    }
    .contact-us-panel span {
        border: 1px solid #fff;
        border-radius: 50px;
        width: 36px;
        height: 36px;
        margin-right: 16px;
    }
    .step_opacity{
        opacity: .3;
    }
    .slide-step{
        width: 100%;
        animation: slide-step 1s ease-in-out;
    }
    @keyframes slide-step {
        from{
            opacity: 0;
        }
    }
    .slide-right{
        width: auto;
        animation: slide-right 1s ease-out;
    }
    @keyframes slide-right {
        from{
            transform: translate(30vw, 0vh);
        }
    }
    .textEnd{
        animation: textEnd 1s ease-out;
    }
    @keyframes textEnd {
        to{
            opacity: 0;
        }
    }

    #c1, #c2, #c3, #c4{
      visibility: hidden;
      position: absolute;
    }
    #c1 + label, #c2 + label,
    #c3 + label, #c4 + label{
      display:block;
      border:1px solid #fff;
    }
    #c1:checked + label, #c2:checked + label,
    #c3:checked + label, #c4:checked + label{
      border: .1px solid var(--v-secondary-base);
      border-radius: 5px;
      color: var(--v-primary-base);
    }
    input[type='radio'][name='budget']{
        visibility: hidden;
        position: absolute;
    }
    input[type='radio'][name='budget'] + label{
      display:block;
      border:1px solid var(--v-secondary-base);
      border-radius: 5px;
      margin-bottom:15px;
    }
    input[type='radio'][name='budget']:checked + label{
        border: 1px solid #ff3f88;
        box-shadow: 0 20px 20px rgba(0,0,0,.05)
    }

    @media (min-width: 1264px) and (max-width: 1600px){
        .balloon{
            width: 80%;
        }
        .cloud{
            width: 35%;
        }
        .cloud.cl1{
            right: 20%;
        }
        .cu-text1, .cu-text2, .cu-text_hide{
            font-size: 3rem !important;
        }
    }
</style>