<template>
    <v-app>
        <v-container>
            <div class="load d-flex align-center justify-center" v-if="showLoading">
                <svg width="180" height="80">
                    <g id="logo_animate1" transform="scale(1, 1)" transform-origin="48px 0" opacity="1"><polygon points="32,26 48,0 64,26" fill="#654da5" stroke-width="0"></polygon></g>
                    <g id="logo_animate2" transform="scale(1, 1)" transform-origin="16px 0" opacity="1"><polygon points="0,26 16,52 32,26" fill="#654da5" stroke-width="0"></polygon></g>
                    <g id="logo_animate3" transform="scale(1, 1)" transform-origin="32px 0" opacity="1"><polygon points="16,52 32,26 48,52" fill="#654da5" stroke-width="0"></polygon></g>
                    <g id="logo_animate4" transform="scale(1, 1)" transform-origin="32px 0" opacity="1"><polygon points="16,52 32,78 48,52" fill="#654da5" stroke-width="0"></polygon></g>
                    <g id="logo_animate5" transform="scale(1, 1)" transform-origin="96px 0" opacity="1"><polygon points="80,52 96,26 112,52" fill="#e85eac" stroke-width="0"></polygon></g>
                    <g id="logo_animate6" transform="scale(1, 1)" transform-origin="112px 0" opacity="1"><polygon points="96,26 112,52 128,26" fill="#e85eac" stroke-width="0"></polygon></g>
                    <g id="logo_animate7" transform="scale(1, 1)" transform-origin="144px 0" opacity="1"><polygon points="128,26 144,0 160,26" fill="#e85eac" stroke-width="0"></polygon></g>
                    <g id="logo_animate8" transform="scale(1, 1)" transform-origin="160px 0" opacity="1"><polygon points="144,52 160,78 176,52" fill="#e85eac" stroke-width="0"></polygon></g>
                </svg>
            </div>

            <Preloader ref="preload" v-if="showPreload"></Preloader>
            <router-view name="header" v-if="visible"></router-view>
            <main v-if="visible">
                <transition name="fade"
                            mode="out-in">
                    <router-view />
                </transition>
            </main>
            <router-view name="footer" v-if="visible"></router-view>
        </v-container>
    </v-app>
</template>
<script>
    import Preloader from "@/components/Preloader.vue";

    export default {
        components: {
            Preloader
        },
        data() {
            return {
                showLoading: true,
                visible: false,
                showPreload: false
            }
        },
        mounted: function () {
            
            this.$nextTick(function () {
                setTimeout(function () {
                    this.showPreload = true;
                }.bind(this), 2000);
                setTimeout(function () {
                    this.showLoading = false;
                    this.visible = true;
                    this.$refs.preload.show = false;
                }.bind(this), 4000);
                setTimeout(function () {
                    this.$refs.preload.show2 = false;
                }.bind(this), 4500);    
            })
        },
    };
</script>
<style>
    .fade-enter-active,
    .fade-leave-active {
        transition-duration: 0.3s;
        transition-property: opacity;
        transition-timing-function: ease;
    }

    .fade-enter,
    .fade-leave-active {
        opacity: 0
    }
    .load{
        height:100vh;
    }
    #logo_animate1 {
        animation-name: diamondScale;
        animation-duration: 2s;
    }
    #logo_animate2 {
        transform: rotate(45deg);
        animation-duration: 4s;
        animation-iteration-count: infinite;
        transform-origin: 50% 50%;
    }
    @keyframes diamondScale {
        0%   { transform: scale(1,1); }
        50%  { transform: scale(0,1); }
        100% { transform: scale(1,1); }
    }
</style>