import Vue from "vue";
import Router from "vue-router";
import AppHeader from "./layout/AppHeader";
import AppFooter from "./layout/AppFooter";
import Home from "./views/Home.vue";
import ContactUs from "./views/ContactUs.vue";
import Products from "./views/Products.vue";

Vue.use(Router);

export default new Router({
    linkExactActiveClass: "active",
    mode: "history",
    routes: [
        {
            path: "/",
            name: "home",
            components: {
                header: AppHeader,
                default: Home,
                footer: AppFooter
            }
        },
        {
            path: "/contact-us",
            name: "contact-us",
            components: {
                header: AppHeader,
                default: ContactUs,
                footer: AppFooter
            }
        },
        {
            path: "/products",
            name: "products",
            components: {
                header: AppHeader,
                default: Products,
                footer: AppFooter
            }
        },
    ],
    scrollBehavior: to => {
        if (to.hash) {
            return { selector: to.hash };
        } else {
            return { x: 0, y: 0 };
        }
    }
});
